* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.home-right {
  box-shadow: 5px 6px 17px 0 rgba(0, 0, 0, 0.11);
  width: 50%;
  padding: 15px;
  border-radius: 7px;
  border: 1px solid #eee;
  float: right;
  height: calc(100vh - 115px);
  background: #fff;
  position: relative;
}

.box-containerr {
  box-shadow: 5px 6px 17px 0 rgba(0, 0, 0, 0.11);
  border-radius: 10px;
  padding: 10px;
  border: 1px solid #eee;
  border-left: 5px solid #f26323;
  font-size: 12px;
  position: relative;
  margin-bottom: 0.5rem;
}

.box-container {
  box-shadow: 5px 6px 17px 0 rgba(0, 0, 0, 0.11);
  border-radius: 10px;
  padding: 10px;
  border: 1px solid #eee;
  border-left: 5px solid green;
  font-size: 12px;
  position: relative;
  margin-bottom: 0.5rem;
}

.box-empty-container {
  position: relative;
  top: 45%;
  text-align: center;
  word-break: break-word;
  left: 10%;
  width: 80%;
  display: block;
}

.bottomPart.consultPart .boxtitle {
  width: 49%;
}

.box-inner {
  height: 100vh;
  overflow-y: auto;
}

.boxTwo {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.boxTwo p:first-child {
  white-space: nowrap;
  width: 95%;
  overflow: hidden;
  text-overflow: ellipsis;
}

.boxTwo p:last-child {
  cursor: pointer;
}

.decBtn button {
  min-width: 100px;
  background: #fff;
  border: 1px solid rgba(3, 87, 113, 1);
  color: rgba(3, 87, 113, 1);
  border-radius: 8px !important;
  height: 1.8rem !important;
  font-weight: 600;
}

.decBtn button a {
  color: rgba(3, 87, 113, 1);
}

.decBtn button a:hover {
  text-decoration: none;
}

.decBtn p {
  text-align: center;
  font-weight: bold;
  color: #24c624;
}

.decBtn {
  position: absolute;
  right: 0;
  top: 0;
}

/* .box-container p {
  margin-bottom: 0.3rem;
} */

.box-container .boxTwo p {
  margin-bottom: 0;
}

.first-part {
  background: radial-gradient(
    100% 100% at 0% 10.61%,
    #6fb0c7 13.98%,
    #035771 87.81%
  );
  color: #fff;
  padding: 15px;
  border-radius: 7px;
  border-bottom-left-radius: 1px;
  border-bottom-right-radius: 1px;
}

.dropdown select {
  width: 27%;
  margin-right: 1rem;
  height: 30px;
  margin-top: 0.5rem;
  border: 1px solid #eee;
  border-radius: 5px;
}

.dropdown button {
  margin-left: 1rem;
  width: 20%;
  background: #f26323;
  color: #fff;
  border: 1px solid #f26323;
  border-radius: 5px !important;
}

.toggleBtn button {
  border-radius: 8px !important;
}

.bottomPrt {
  border-top: 1px solid #094c6173;
  padding: 0.5rem 1rem;
  background: #035771e8;
  color: #fff;
  border-bottom-left-radius: 7px;
  border-bottom-right-radius: 7px;
}

.bottomPrt button {
  background: #fff;
  color: #f26323;
  border: 1px solid #fff;
  min-width: 150px;
  margin-left: 1rem;
  font-weight: bold;
}

.bottomPart .boxOne {
  display: flex;
  flex-wrap: wrap;
  font-weight: 600;
}

.bottomPart .box-title {
  width: 32%;
  word-break: break-all;
  padding-right: 10px;
}

.bottomPart .box-title p:first-child {
  color: #999696;
}

.bottomPart .boxtitle p:first-child {
  color: #999696;
}

.bottomPart .decBtn {
  bottom: 0;
  top: inherit;
  right: 0;
}

.bottomPart {
  height: calc(100vh - 465px);
  overflow-y: auto;
}

.toggleBtn .active {
  background: #035771;
  color: #fff;
}

.toggleBtn .notactive {
  background: #fff;
  color: #035771;
}

.toggleBtn button:first-child {
  width: 50%;
  border: 1px solid #035771;
  border-bottom-right-radius: 1px !important;
  border-top-right-radius: 1px !important;
  cursor: pointer;
  box-shadow: 5px 6px 17px 0 rgba(0, 0, 0, 0.11);
}

.toggleBtn button:last-child {
  width: 50%;
  border: 1px solid #03577170;
  border-bottom-left-radius: 1px !important;
  border-top-left-radius: 1px !important;
  cursor: pointer;
  box-shadow: 5px 6px 17px 0 rgba(0, 0, 0, 0.11);
}

.first-part p:nth-child(2) {
  padding-top: 14px;
  font-size: 13px;
  padding-bottom: 14px;
}

.first-part p:first-child span img {
  width: 17px;
  margin-right: 0.4rem;
}

.first-part p:first-child span {
  font-weight: bold;
}

.first-part p:first-child {
  font-family: "GothamNarrowBold", sans-serif;
}

.first-part p:nth-child(2) {
  padding-top: 14px;
  font-size: 13px;
  padding-bottom: 14px;
}

.clickBtn {
  /* margin-right: 1rem; */
  width: 20%;
  background: #f26323;
  color: #fff;
  border: 1px solid #f26323;
  border-radius: 5px !important;
}

.seeAll {
  position: absolute;
  bottom: 48px;
  right: 30%;
  z-index: 99;
  min-width: 200px;
  width: 200px;
}

@media screen and (min-width: 320px) and (max-width: 360px) {
  .tooltip .tooltiptext {
    width: 250px !important;
    bottom: 32px;
    right: -15px !important;
    top: inherit !important;
  }

  .dropdown select:nth-child(2) {
    width: 77% !important;
  }
}
@media screen and (min-width: 1025px) and (max-width: 1400px) {
  .tooltip .tooltiptext {
    min-width: 250px;
    width: 250px !important;
    right: 56% !important;
  }
}

@media screen and (max-width: 767px) {
  .dropdown select:first-child {
    width: 100%;
  }

  .tooltip .tooltiptext {
    width: 250px !important;
    bottom: 32px;
    right: 0 !important;
    top: inherit !important;
  }

  .home-right {
    width: 100%;
    background: #fff;
  }

  .dropdown select:nth-child(2) {
    width: 82%;
  }

  .dropdown button {
    width: 55%;
    margin-left: 0;
    margin-top: 10px;
  }

  .bottomPrt button {
    padding: initial;
    margin-left: 1rem;
    font-weight: bold;
    font-size: 12px;
    display: flex;
    min-width: 85px;
    align-items: center;
    justify-content: space-evenly;
  }

  .bottomPrt p {
    font-size: 12px;
  }

  .bottomPrt {
    padding: 0.4rem;
  }

  .toggleBtn button:first-child,
  .toggleBtn button:last-child {
    font-size: 12px;
  }

  .bottomPart .box-title {
    width: 50%;
  }

  .bottomPart .box-title p {
    font-size: 11px;
  }

  .bottomPart .boxtitle p {
    font-size: 11px;
  }

  .first-part p:nth-child(2) {
    font-size: 12px;
  }
}

.tooltip {
  position: relative;
  display: inline-block;
  font-size: 13px;
  opacity: 1;
}

.tooltip:hover .tooltiptext {
  visibility: visible;
}

.tooltip .tooltiptext {
  visibility: hidden;
  width: 405px;
  background-color: #fff;
  color: #000;
  text-align: center;
  border-radius: 13px;
  padding: 0;
  position: absolute;
  z-index: 1;
  top: 17px;
  right: 136%;
  box-shadow: 0 0 12px 0 #00000030 !important;
}

.modal-content2 {
  padding: 0 10px 10px 10px;
}

.blue-bg {
  background: #0072ce;
  color: #fff;
  text-align: center;
  padding: 10px 0;
  font-size: 18px;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
  position: relative;
}

.modal-content2 {
  background-color: #fff;
  border-bottom-right-radius: 14px !important;
  border-bottom-left-radius: 14px !important;
  border: 1px solid #0003;
}

.consultation-type-blk {
  width: 32%;
  flex: 0 0 32%;
}

.schdule_appoinment {
  padding-top: 8px;
}

.schedule-img img {
  border-radius: inherit;
}

.consultation-type {
  color: #0072ce;
  font-size: 12px;
  font-family: GothamNarrowBold, sans-serif;
}

.selected_appoinment {
  color: #3e3e3e;
  font-family: GothamNarrowBook, sans-serif;
}

.tooltip .modal-title {
  position: inherit;
}

.tooltiptext .row {
  margin: initial !important;
}

select:disabled {
  opacity: 0.9;
}

.zoomBtn {
  margin: 10px;
}

@media screen and (max-height: 600px) {
  .whole-wrapper {
    height: calc(100vh - 206px);
    overflow-y: auto;
  }

  .bottomPart {
    height: 30vh;
    overflow-y: auto;
  }

  .dropdown button {
    width: 25%;
  }

  nav#sidebar {
    overflow-y: scroll;
  }
}
